/* eslint-disable max-len */
import { NgModule } from '@angular/core';
import { NoPreloading, PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuardGuard } from './guards/auth-guard.guard';
import { Role } from './shared/constants/role.constants';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'login',
    loadChildren: () => import('./pages/login/login.module').then(m => m.LoginPageModule)
  },
  {
    path: 'password-reset',
    loadChildren: () => import('./pages/password-reset/password-reset.module').then(m => m.PasswordResetPageModule),
  },
  {
    path: 'home',
    loadChildren: () => import('./pages/home/home.module').then(m => m.HomePageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'perfil',
    loadChildren: () => import('./pages/perfil/perfil.module').then(m => m.PerfilPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'administracao-financeira',
    loadChildren: () => import('./pages/administracao-financeira/administracao-financeira.module').then(m => m.AdministracaoFinanceiraPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
    canActivate: [AuthGuardGuard],
    data: {
      role: [Role.ADMIN, Role.SUPERVISOR, Role.ADMIN_TI],
    }
  },
  {
    path: 'administracao-recurso',
    loadChildren: () => import('./pages/administracao-recurso/administracao-recurso.module').then(m => m.AdministracaoRecursoPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'administracao-geral',
    loadChildren: () => import('./pages/administracao-geral/administracao-geral.module').then(m => m.AdministracaoGeralPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'administracao-operacao',
    loadChildren: () => import('./pages/administracao-operacao/administracao-operacao.module').then(m => m.AdministracaoOperacaoPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'carga-dados',
    loadChildren: () => import('./pages/carga-dados/carga-dados.module').then(m => m.CargaDadosPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
  },
  {
    path: 'administracao-ti',
    loadChildren: () => import('./pages/administracao-ti/administracao-ti.module').then(m => m.AdministracaoTiPageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'comunicacao-email',
    loadChildren: () => import('./pages/comunicacao-email/comunicacao-email.module').then(m => m.ComunicacaoEmailPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
  },
  {
    path: 'prestacao-servico-simples',
    loadChildren: () => import('./pages/prestacao-servico-simples/prestacao-servico-simples.module').then(m => m.PrestacaoServicoSimplesPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
  },
  {
    path: 'prestacao-servico-avancado',
    loadChildren: () => import('./pages/prestacao-servico-avancado/prestacao-servico-avancado.module').then(m => m.PrestacaoServicoAvancadoPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
  },
  {
    path: 'prestacao-servico-gestao',
    loadChildren: () => import('./pages/prestacao-servico-gestao/prestacao-servico-gestao.module').then(m => m.PrestacaoServicoGestaoPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
  },
  {
    path: 'administracao-pagamento',
    loadChildren: () => import('./pages/administracao-pagamento/administracao-pagamento.module').then(m => m.AdministracaoPagamentoPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
  },
  {
    path: 'fluxo-pagamento',
    loadChildren: () => import('./pages/fluxo-pagamento/fluxo-pagamento.module').then(m => m.FluxoPagamentoPageModule),
    // canLoad: [AuthGuardGuard],
    canLoad: [false],
  },
  {
    path: 'administracao-cliente',
    loadChildren: () => import('./pages/administracao-cliente/administracao-cliente.module').then(m => m.AdministracaoClientePageModule),
    canLoad: [AuthGuardGuard],
  },
  {
    path: 'consulta-rapida',
    loadChildren: () => import('./pages/consulta-rapida/consulta-rapida.module').then( m => m.ConsultaRapidaPageModule),
    canLoad: [AuthGuardGuard],
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules, useHash: true })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
